@font-face {
  font-family: robotoLight;
  src: url("./assets/fonts/Roboto_Condensed/static/RobotoCondensed-Light.ttf");
}
@font-face {
  font-family: robotoBold;
  src: url("./assets/fonts/Roboto_Condensed/static/RobotoCondensed-ExtraBold.ttf");
}
h6, h5, h4, h3, h2, h1, p {
  font-family: robotoLight, cursive;
  margin: 0;
  text-align: center;
}

div, section, aside, nav, header, footer, main {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}
@media (max-width: 1100px) {
  html {
    font-size: 12px;
  }
}
@media (max-width: 900px) {
  html {
    font-size: 10px;
  }
}

.containerForSubPage {
  padding: 100px var(--leftAndRightPadding) 40px var(--leftAndRightPadding);
  display: flex;
  flex-grow: 10;
  position: relative;
}
.containerForSubPage.withImage {
  padding-top: 0;
}
@media (max-width: 800px) {
  .containerForSubPage {
    padding: 60px var(--leftAndRightPadding) 40px var(--leftAndRightPadding);
  }
}

.flexibleBlock {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100vh;
}

