.container {
  background-image: url("../../../../assets/images/makeAdonat.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding: 20px var(--leftAndRightPadding);
  display: flex;
  justify-content: center;
  z-index: 400;
  position: relative;
  column-gap: 10px;
}
@media (max-width: 700px) {
  .container {
    flex-direction: column;
  }
}
.container .blueBackground {
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 3, 51, 0.36);
  z-index: 500;
  position: absolute;
  top: 0;
  left: 0;
}

