.container {
  display: flex;
  flex-direction: column;
  height: 450px;
  justify-content: center;
  row-gap: 80px;
  align-content: space-between;
  @media (max-width: 900px) {
    height: 330px;
    row-gap: 45px;
  }
  @media (max-width: 700px) {
    height: 150px;
    row-gap: 20px;
  }

  h3 {
    width: 60%;
    color: white;
    text-shadow: 0px 1px 1px black;
    font-size: 1.8rem;
    line-height: 1.5;
    position: relative;
    text-align: left;
    z-index: 600;
    @media (max-width: 700px) {
      width: 100%;
    }

    &:after {
      display: block;
      content: "";
      width: 100%;
      height: 2px;
      background-color: #e0e0e0;
      position: absolute;
      bottom: -40px;
      @media (max-width: 700px) {
        bottom: -10px;
      }
    }
  }

  p {
    width: 60%;
    color: white;
    font-size: 1.6rem;
    line-height: 1.2;
    text-align: left;
    z-index: 600;
    text-shadow: 0px 1px 1px black;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
}
